body {
  display: flex;
  flex-direction: column;
  min-height: 100vh; }

main {
  flex: 1 0 auto;
  padding-top: 30px; }

form .search {
  border-radius: 25px;
  border: 2px solid #E57373;
  box-shadow: none;
  box-sizing: content-box;
  font-size: 1rem;
  height: 3rem;
  margin: 0 0 0 0;
  outline: none;
  padding: 5px;
  transition: all 0.3s;
  width: 100%;

  &:focus:not([readonly]) {
    border: 2px solid #B75C5C;
    box-shadow: none; } }

.comments {
  height: 300px;
  overflow-y: scroll; }

.logout {
  background: none!important;
  border: none;
  color: #26a69a;
  display: block;
  font-size: 16px;
  line-height: 22px;
  padding-top: 0px;
  padding: 14px 16px;
  &:hover {
    background-color: rgba(0, 0, 0, 0.1); } }

.avatar {
  border-radius: 50%;
  float: left;
  height: 150px;
  margin-right: 25px;
  width: 150px; }
